.promo {
  display: block;
  box-sizing: border-box;
  width: @mobile-width - (@mobile-padding * 2);
  margin: 0 auto;
  padding: 43px 60px;
  padding-top: 187px;
  background-color: @basic-green;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-background-phone2.jpg");
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;

  @media (min-width: @tablet-width) {
    width: @tablet-width - (@tablet-padding * 2);
    padding-left: 422px;
    padding-right: 124px;
  }

  @media (min-width: @desktop-width) {
    width: auto;
    padding: 0 42px;
    padding-top: 184px;
  }
}

.promo__title {
  margin: 0;
  position: relative;
  display: block;
  box-sizing: border-box;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-align: center;
  color: @basic-white;

  &::before {
    content: "";
    position: absolute;
    width: 78px;
    height: 80px;
    background-image: url("../img/icon_gift.svg");
    top: -108px;
    left: 39px;
  }
}
